.dropdown {
  display: none;

  &-content {
    @include laptop {
      display: none;
      position: absolute;
      background-color: $background-color-primary;
      width: 131px;
      z-index: 1;
      border: 1px solid rgba(224, 225, 226, 1);
      border-radius: 8px;
      a {
        color: $color-text-primary;
        padding: 12px 16px;
        text-decoration: none;
        display: block;
        &:hover {
          background-color: $background-color-secondary;
        }
      }
    }
  }

  @include laptop {
    margin-left: auto;
    position: relative;
    display: inline-block;
    &:hover {
      .dropdown-content {
        display: block;
      }
    }
  }
}

.sidebar {
